.header
  position: relative
  display: flex
  justify-content: space-between
  align-items: center
  padding: 16px

  &_name
    font-weight: 700
    font-size: clamp(16px, 4vw, 32px)
    color: black

  &_navigation
    display: flex
    gap: 8px

  &-mobile
    position: absolute
    top: 60px
    right: -100%
    // background-color: $light-background
    // border-radius: 12px
    // box-shadow: $card-shadow
    padding: 16px
    transition: right 0.4s ease-out
    &--opened
      right: 0px
      transition: right 0.3s ease-out
